<style scoped>
.subCol li {
  margin: 0 -18px;
  list-style: none;
  text-align: center;
  /* padding: 9px; */
  border-bottom: 1px solid #ccc;
  overflow-x: hidden;
  height: 45px;
  line-height: 44px;
}
.subCol li:last-child {
  border-bottom: none;
}
.Mustfill{
  color: red;
}
.Statisticsbox{
  height: 48px;
  background: #f8f8f9;
  line-height: 48px;
  text-indent: 20px;
  border:1px solid #e8eaec;
  border-top: none;
}
.Statisticsbox .Statisticspan{
  padding-right: 30px;
}
#tablebox td{
  font-size: 12px;
}
.changeboxcodeicon:hover{
  cursor: pointer;
}
</style>
<template>
  <div>
     <div id="tablebox">
         <Table
                @on-selection-change='selectionchange'
                :columns="Normal"
                :data="NormalData"
                :border="NormalData.length > 0"
                :loading="loading"
              >
               <template slot-scope="{ row, index }" slot="orderid">
                  <span>{{row.TinyOrderID}}</span>
                </template>
                 <template slot-scope="{ row, index }" slot="BoxCode">
                  <ul class="subCol">
                    <li v-for="item in row.Groups">
                      {{item.BoxCode|showboxcode}}
                       <Icon class="changeboxcodeicon" type="md-create" v-if="row.IsAbleDeclare==true" @click="changeboxcode(item)" />
                    </li>
                   
                  </ul>
                </template>
                <template slot-scope="{ row, index }" slot="PartNumber">
                   <ul class="subCol">
                    <li v-for="item in row.Groups">{{item.Product.PartNumber}}
                        <span v-if="item.Condition!=null">
                          <Tag v-if="item.Conditions.IsCIQ==true" color="primary"></Tag>
                          <Tag v-if="item.Conditions.IsCCC==true" color="warning">CCC</Tag>
                          <Tag v-if="item.Conditions.IsEmbargo==true" color="error"></Tag>
                          <Tag v-if="item.Conditions.IsHighPrice==true" color="magenta"></Tag>
                        </span>
                    </li>
                  </ul>
                </template>
                <template slot-scope="{ row, index }" slot="Manufacturer">
                  <ul class="subCol">
                    <li v-for="item in row.Groups">{{item.Product.Manufacturer}}</li>
                  </ul>
                </template>
                <template slot-scope="{ row, index }" slot="DateCode">
                  <ul class="subCol">
                    <li v-for="item in row.Groups">{{item.DateCode}}</li>
                  </ul>
                </template>
                <template slot-scope="{ row, index }" slot="Origin">
                  <ul class="subCol">
                    <li v-for="item in row.Groups">{{item.Origin}}</li>
                  </ul>
                </template>
                <template slot-scope="{ row, index }" slot="Weight">
                  <ul class="subCol">
                    <li v-for="item in row.Groups">{{item.Weight}}</li>
                  </ul>
                </template>
                <template slot-scope="{ row, index }" slot="Volume">
                  <ul class="subCol">
                    <li v-for="item in row.Groups">{{item.Volume}}</li>
                  </ul>
                </template>
                <template slot-scope="{ row, index }" slot="Quantity">
                   <ul class="subCol">
                    <li v-for="item in row.Groups">{{item.YdQty}}</li>
                  </ul>
                </template>
                <template slot-scope="{ row, index }" slot="PickedQuantity">
                  <ul class="subCol">
                    <li v-for="(item,index) in row.Groups" >
                      <Input style="width:70%" v-if="itemindex==index&&showinput==true" type="number" @on-blur="ok_changeQty(item)" @on-enter="ok_changeQty(item)" v-model="item.SdQty"/>
                      <p v-else>
                        <span >{{item.SdQty}}</span>
                        <!-- <Icon v-if="row.IsAbleDeclare==true" type="md-create" @click="changeQuantityBtn(index)"/> -->
                      </p>
                    </li>
                  </ul>
                </template>
                <template slot-scope="{ row, index }" slot="CreateDate">
                  <ul class="subCol">
                    <li v-for="item in row.Groups">{{item.EnterDate|showDateexact}}</li>
                  </ul>
                </template>
                <template slot-scope="{ row, index }" slot="CarrierName">
                  <ul class="subCol">
                    <li v-for="item in row.Groups">{{item.CarrierName}}</li>
                  </ul>
                </template>
                <template slot-scope="{ row, index }" slot="code">
                  <ul class="subCol">
                    <li v-for="item in row.Groups">{{item.wbCode}}</li>
                  </ul>
                </template>
                <template slot-scope="{ row, index }" slot="Action">
                  <ul class="subCol">
                    <li v-for="item in row.Groups">
                      <Button
                        type="error"
                        size="small"
                        @click="delItem(item)"
                        :disabled="item.Display==false?true:false"
                      ></Button>
                    </li>
                  </ul>
                </template>
              </Table>
              <p class="Statisticsbox" v-if="NormalData.length > 0">
                <span class="Statisticspan"></span>
                <span class="Statisticspan">(Kg){{Normalinfo.TotalWeight}}</span>
                <span class="Statisticspan">{{Normalinfo.TotalPart}}</span>
                <span class="Statisticspan">{{Normalinfo.TotalQuantity}}</span>
                <Button type="primary" icon='md-checkmark' :disabled='Normalinfo.IsDisabledDeclareBtn==false?false:true' @click="ok_Sealing"></Button>
              </p>
     </div>
     <Modal
        v-model="showEdit"
        title=""
        :mask-closable="false"
        @on-visible-change='visiblechange'
        >
      <span slot="close">
           <Icon type="ios-close" @click="cancel"/>
       </span>
      
      <p style="padding-top:10px;">
        <label>
          <em class="Mustfill">*</em>
        </label> 
        <DatePicker type="date" style="width:80%"  :options="options3" placeholder="" :clearable='false' :value="saleDate" @on-change='changeData'></DatePicker>
      </p> 
     <p style="padding-top:10px;">
       <label>
        <em class="Mustfill">*</em>
      </label> 
       <Input
        v-model.trim="newboxcode" 
        maxlength="30" 
        placeholder="" 
        style="width:80%" 
        @on-blur='handleCreate1(newboxcode)' />
     </p>
     <div slot="footer">
        <Button @click="cancel"></Button>
        <Button type="primary" @click="ok_change"></Button>
     </div>
    </Modal>

    <!--   -->
     <Modal
        v-model="changeQuantity"
        title=""
        :mask-closable='false'
       >
        <div>
          <label for="">:</label>
          <Input v-model="NewQuantity" placeholder="" />
        </div>
        <div slot="footer">
           <Button></Button>
           <Button type="primary"></Button>
        </div>
    </Modal>
    <!--   -->
    <Modal
        v-model="showsealing"
        @on-ok="ok_sealing"
        @on-cancel="cancel_sealing">
        <div slot="header">
          <Icon type="md-help-circle" style="font-size:28px;color:#f90" />
          <span style="font-size: 15px;line-height: 26px;vertical-align: bottom; color: #0c0c0c; font-weight: bold;"></span>
        </div>
        <p v-for="item in overweight">{{item.TinyOrderID}} 
          <span style="padding-right:5px">{{item.TotalWeight}}</span>
          <span style="padding-right:5px">{{item.TotalAvgWeight}}</span></p>
    </Modal>
  </div>
</template>
<script>
import { TinyOrderIDReportGroup, DeletePicking,BoxcodeEnter,CgBoxesShow,BoxcodeDelete,ModifyQuantity,cgCustomsStorageCloseBoxes} from "../../api/CgApi"; //api 
export default {
  name: "Sealing",
  props: ["OrderID", "Type",'EnterCode','hietorgetDetail','WaybillID','ExcuteStatus'],
  data() {
    return {
      options3: {
          disabledDate (date) {
              return date && date.valueOf() < Date.now() - 86400000;
          }
      },

      itemindex:-1,
      showinput:false,
      changeQuantity:false,//
      NewQuantity:null,//
      selectdata:[],//
      saleDate:'',//
      Operateddisable:true,//
      isclickbtn:true,
      isSealingclick:true,//
      enterCode:null,
      boxingarr:[],
      houseid:sessionStorage.getItem("UserWareHouse"),
      AdminID:sessionStorage.getItem("userID"),
      oldboxcode:'',
      newboxcode:'',
      newboxcodeback:null,
      oldBoxCode2:null,//
      boxcodetype:'1',
      showEdit:false,
      Nameitem:'0',
      loading: true,
      warehouseId: null,
      waybillinfo: {},
      NormalData: [], //
      AbnormalData: [], //
      Normalinfo:null,
      Abnormainfo:null,
      Normal: [
        {
          type: 'selection',
          width: 60,
          align: 'center'
        },
        {
          title: "",
          slot: "orderid",
          align: "center"
        },
        {
          title: "",
          slot: "BoxCode",
          align: "center"
        },
        {
          title: "",
          slot: "PartNumber",
          align: "center",
          width:180
        },
        {
          title: "",
          slot: "Manufacturer",
          align: "center"
        },
        {
          title: "",
          slot: "DateCode",
          align: "center"
        },
        {
          title: "",
          slot: "Origin",
          align: "center"
        },
        {
          title: "(Kg)",
          slot: "Weight",
          align: "center"
        },
        {
          title: "",
          slot: "Volume",
          align: "center"
        },
        {
          title: "",
          slot: "Quantity",
          align: "center"
        },
        {
          title: "",
          slot: "PickedQuantity",
          align: "center",
           width:130
        },
        {
          title: "",
          slot: "CarrierName",
          align: "center",
          width:150
        },
        {
          title: "",
          slot: "code",
          align: "center",
          width:150
        },
        {
          title: "",
          slot: "CreateDate",
          align: "center",
           width:145
        }
      ],
      showsealing:false,
      overweight:[],
    
    };
  },
  created() {
  },
  computed:{
    getExcuteStatus(){
      console.log(this.ExcuteStatus)
      return this.ExcuteStatus
    }
  },
  mounted() {
    this.getlistdata()
    console.log(this.getExcuteStatus)
    if (this.Type == 1) {
      this.Normal[7].title = "";
      this.Abnormal[7].title = "";
    } else {
      var CarrierNamedatas = {
        title: "",
        slot: "CarrierName",
        align: "center",
        width:150
      };
      // this.Normal.push(CarrierNamedatas);
    //   this.Abnormal.push(CarrierNamedatas);
       var codedatas = {
        title: "",
        slot: "code",
        align: "center",
         width:150
      };
      // this.Normal.push(codedatas);
    //   this.Abnormal.push(codedatas);
      var datas = {
        title: "",
        slot: "Action",
        align: "center"
      };
      this.Normal.push(datas);
    //   this.Abnormal.push(datas);
    }
  },
  methods: {
     setboxsplit(str) {
      //
      if(str){
         return str.split("]")[1]
      }
    },
    getlistdata(){
      TinyOrderIDReportGroup(this.OrderID).then(res=>{
        this.loading=false
        this.Normalinfo = res;
        this.NormalData=res.TinyOrderID_Group
      })
    },
    delItem(item) {
      this.$Modal.confirm({
          title: '',
          // content: '<p>Content of dialog</p><p>Content of dialog</p>',
          onOk: () => {
            // console.log('')
              var data = {
                StorageID:item.StorageID,
                PickingID:item.PickingID,
                AdminID:sessionStorage.getItem("userID")
              };
              DeletePicking(data).then(res => {
                  if(res.success==true){
                    this.$Message.success('');
                    this.loading=true
                    // this.changeTabs(this.Nameitem)
                    this.getlistdata()
                    this.$emit('hietorgetDetail')
                  }else if(res.success==false){
                    this.$Message.error(res.data);
                  }
              });
          },
          onCancel: () => {
              // this.$Message.info();
          }
      });
    },
    changeboxcode(value){
      console.log(value)
      this.showEdit=true
      this.oldboxcode=value
    },
    handleCreate1(val) {  //
      if(val!=''&&val!=null){
         if(this.oldBoxCode2!=null){
               var data={
                    boxCode:this.oldBoxCode2,
                    // date:this.saleDate
                  }
                  BoxcodeDelete(data).then(res=>{
                    var newdata={
                       enterCode:this.EnterCode, // entercode
                       code:val, // 
                       date:this.saleDate, //
                       adminID:sessionStorage.getItem("userID"),//adminID
                    }
                    BoxcodeEnter(newdata).then(res=>{
                      console.log(res)
                      if(res.success==false&&res.code==400){
                        this.$Message.error("");
                        this.newboxcode=null;
                        this.newboxcodeback=null
                        this.oldBoxCode2=null;
                        this.isclickbtn=false
                      }else{
                        this.newboxcode=this.setboxsplit(res.boxCode)
                        this.newboxcodeback=res.boxCode
                        this.oldBoxCode2=res.boxCode
                        this.isclickbtn=true
                      }
                    })
                })
              }else{
                 var data={
                     enterCode:this.EnterCode, // entercode
                    code:val, // 
                    date:this.saleDate, //
                    adminID:sessionStorage.getItem("userID"),//adminID
                  }
                  BoxcodeEnter(data).then(res=>{
                    if(res.success==false){
                    this.$Message.error(res.data)
                    this.newboxcode=null
                    this.newboxcodeback=null
                    this.isclickbtn=false
                    }else{
                      this.newboxcode=this.setboxsplit(res.boxCode)
                      this.newboxcodeback=res.boxCode
                      this.oldBoxCode2=res.boxCode
                      this.isclickbtn=true
                    }
                  })
              }
            
           
         
      }
      
    },
     CgBoxesShow() {
      var data = {
        enterCode: this.EnterCode, //
        date: "", //
      };
        console.log(this.EnterCode)
        CgBoxesShow(data).then(res => {
          if (res.length > 0) {
            this.boxingarr = res;
          }else{
            this.boxingarr=[]
          }
        });
    },
    visiblechange(value){
      if(value==true){
        // this.CgBoxesShow()
        this.showchangebox = true;
        const myDate = new Date();
        const year = myDate.getFullYear(); // 
        const month = myDate.getMonth() + 1; // (0-11,011);
        const day = myDate.getDate(); // 1-31
        // 2019/07/29 - 2019/07/29
        this.saleDate = `${year}/${month}/${day}`;
      }else{
        this.newboxcode=null
        this.oldBoxCode2=null
        this.newboxcodeback=null
        this.boxcodetype='1'
      }
    },
     // 
    BoxcodeDeletefun(){
      var data={
        boxCode:this.newboxcodeback,
        // date:this.saleDate
      }
      BoxcodeDelete(data).then(res=>{
        console.log(res)
      })
    },
    oldBoxDelete(val){
      var data={
        boxCode:val,
        // date:this.saleDate
      }
      BoxcodeDelete(data).then(res=>{
      })
    },
    cancel(){
      this.BoxcodeDeletefun()
      this.showEdit=false
      this.oldBoxCode2=null
      this.newboxcodeback=null
    },
    // 
    ok_change(){
      if(this.newboxcode!=null&&this.newboxcode!=''){
        if(this.isclickbtn==true&&this.newboxcodeback!=null){
          var data={
          date:this.saleDate,
          adminID:sessionStorage.getItem("userID"), //adminID
          storageID:this.oldboxcode.StorageID, //, ID
          oldBoxCode:this.oldboxcode.BoxCode, //, ID
          newBoxCode:this.newboxcodeback, //, ID
          source:60, /// ---->, , 30,  60
          }
          BoxcodeEnter(data).then(res=>{
              if(res.success==false){
                this.$Message.error(res.data)
                this.newboxcodeback=null
                this.showEdit=true
              }else{
                this.$Message.success('')
                this.loading=true
                 this.getlistdata()
                this.showEdit=false
              }
          })
        }
        
      }else{
        this.$Message.error('')
      }
      
    },
      changeData(val){
      this.saleDate=val
      if(this.newboxcode!=''&&this.newboxcode!=null){
        this.handleCreate1(this.newboxcode)
      }
    },
    ok_Sealing(){
      if(this.selectdata.length<=0){
        // this.$Message.error('')
        this.$Message.warning({
                content: '',
                duration: 3
        });
      }else{
      var CloseBoxesArr=[];
      this.overweight=[]
      var newsrr=this.selectdata
      for(var i=0,lens=newsrr.length;i<lens;i++){
        CloseBoxesArr.push(newsrr[i].TinyOrderID)
        if(newsrr[i].TotalWeight<(newsrr[i].TotalAvgWeight-newsrr[i].TotalAvgWeight*(1/2))||newsrr[i].TotalWeight>(newsrr[i].TotalAvgWeight+newsrr[i].TotalAvgWeight*(1/2))){
          this.overweight.push(newsrr[i])
        }
      }
      var data={
        AdminID:sessionStorage.getItem("userID"),
        TinyOrderID:CloseBoxesArr,
        WaybillID:this.WaybillID
      }
      console.log(this.overweight)
      if(this.overweight.length>0){
         this.showsealing=true
      }else{
        this.showsealing=false
        this.$Modal.confirm({
          title: '',
          // content: ``,
          onOk: () => {
              this.isSealingclick=false;
              this.cgCustomsStorageCloseBoxes(data)
          },
          onCancel: () => {
              // this.$Message.info('Clicked cancel');
          }
        });
      }
        // this.$Modal.confirm({
        //   title: '',
        //   // content: '',
        //   onOk: () => {
        //       this.isSealingclick=false;
        //       var CloseBoxesArr=[];
        //       var newsrr=this.selectdata
        //       for(var i=0,lens=newsrr.length;i<lens;i++){
        //         CloseBoxesArr.push(newsrr[i].TinyOrderID)
        //       }
        //       var data={
        //         AdminID:sessionStorage.getItem("userID"),
        //         TinyOrderID:CloseBoxesArr,
        //         WaybillID:this.WaybillID
        //       }
        //       console.log(data)
        //       this.cgCustomsStorageCloseBoxes(data)
        //   },
        //   onCancel: () => {
        //       // this.$Message.info('Clicked cancel');
        //   }
        // });
      }
    },
    ok_sealing(){
      this.isSealingclick=false;
      var CloseBoxesArr=[];
      var newsrr=this.selectdata
      for(var i=0,lens=newsrr.length;i<lens;i++){
        CloseBoxesArr.push(newsrr[i].TinyOrderID)
      }
      var data={
        AdminID:sessionStorage.getItem("userID"),
        TinyOrderID:CloseBoxesArr,
        WaybillID:this.WaybillID
      }
      console.log(data)
      this.cgCustomsStorageCloseBoxes(data)
    },
    cancel_sealing(){
      this.showsealing=false
    },
     // 
    cgCustomsStorageCloseBoxes(data){
      cgCustomsStorageCloseBoxes(data).then(res=>{
        if(res.success==true){
          this.loading=true
          this.getlistdata()
          this.$Message.success('');
           this.selectdata=[]
        }else{
          this.$Message.error({
                content: res.data,
                duration: 3
          });
        }
        this.isSealingclick=true;
      })
    },
    selectionchange(val){
      this.selectdata=val
      console.log(this.selectdata.length)
    },

    changeQuantityBtn(index){
      this.itemindex=index;
      this.showinput=true;
    },
    ok_changeQty(val){
      console.log(val)
      if(val.SdQty!=''&&val.SdQty!=null){
        this.loading=true
        var AdminID=sessionStorage.getItem("userID");
        var data={
          StorageID:val.StorageID,
      AdminID:AdminID,
      Quantity:val.SdQty
        }
        this.chengeModify(data)
      }else{
        this.$Message.warning({
                content: '',
                duration: 3
        });
      }
    },
    //
    chengeModify(data){
      ModifyQuantity(data).then(res=>{
        console.log(res)
        if(res.Success==true){
          this.itemindex=-1;
          this.showinput=false;
          this.getlistdata()
        }else{
          this.$Message.error({
                content: res.Data,
                duration: 3
          });
        }
      })
    },
  }
};
</script>   
<style scoped>
.detailtitle {
  line-height: 24px;
  border-left: 5px solid #2d8cf0;
  font-weight: bold;
  font-size: 16px;
  text-indent: 10px;
}
.detail_li .itemli {
  line-height: 45px;
  font-size: 14px;
}
</style>


